@import '../../styles/_colors';
@import '../../styles/_fonts';





.custom-shape-divider-bottom-1626950069 {
    position: relative;
    width: 100%;
    transform: rotate(180deg);
}

.custom-shape-divider-bottom-1626950069 svg {
    position: absolute;
    display: block;
    width: calc(158% + 1.3px);
    height: 70px;
    overflow: hidden;
}

.custom-shape-divider-bottom-1626950069 .shape-fill {
    fill: #75559833;
}





.SliderBackground{
    background: rgba(117, 85, 152, 0.2);
}

.SliderContainer {
    padding: 2rem 0;
    margin: 0 auto;
    max-width: 1140px;
    height: auto;

    ;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    
    text-align: center;
    background: rgba(117, 85, 152, 0.2);
    border-radius: 1rem;

    display: flex;
    flex-direction: column;

    h3{
        padding: 1rem;
        color: $lila;
    }

    img {
        padding: 1rem;
        margin: 0 auto;
        display: block;
        width: 40%;
        height: 40%;
        object-fit: cover;
    }

    p{ 
        padding: 1rem;
        color: rgba(117, 85, 152);;
        font-size: 1rem;
        padding-bottom: 3rem;
        
    }
}

.swiper-button-prev, .swiper-button-next{
    color: $lila;
    
}
.swiper-pagination-bullet-active{
    background: $lila;
}

@media screen and (min-width: 320px) {
    .swiper-container {
    width: 320px;
   
    }

}
@media screen and (min-width: 768px) {
.swiper-container {
    width: 768px;
  
}
}

@media screen and (min-width: 1024px) {
    .swiper-container {
        width: 1024px;
       
    }
}




.custom-shape-divider-top-1626950293 {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.custom-shape-divider-top-1626950293 svg {
    position: relative;
    display: block;
    width: calc(158% + 1.3px);
    height: 70px;
}

.custom-shape-divider-top-1626950293 .shape-fill {
    fill: #75559833;
}